const buscar = document.querySelector('.buscar');
const search = document.querySelector('.busqueda-palabra');
const url = "https://api.deezer.com/search?q=";
const musics = document.querySelector('.musics');
const fondoBoton = document.querySelector('.btn-image');
const imgFooter = document.querySelector('.img-footer');
const song = document.querySelector('.song');
const songInfo = document.querySelector('.songInfo');
const play  = document.querySelector('.play');
const playIcon = document.querySelector('.playIcon');
const pause = document.querySelector('.pause');
const rango = document.querySelector('.rango');
const vol = document.querySelector('.vol');
const fondoPanel = document.querySelector('.contenedor-info-artist');
const mp3 = document.querySelector('.mp3');
const artistaP = document.querySelector('.artistaP');
const mejor = document.querySelector('.mejor');
const seguidores = document.querySelector('.seguidores');
const left = document.querySelector('.left');
const right = document.querySelector('.right');
const section2Play  = document.querySelector('.section2_play');
const section2Pause  = document.querySelector('.section2_pause');
let position = 0;

const audio = document.querySelector('.audio');
const parlante = document.querySelector('.parlante');
const parlanteOff = document.querySelector('.parlanteOff');

buscar.addEventListener('click', e => {
    busqueda();

    search.value = "";
    

    
});
//** INPUT BUSCAR */
search.addEventListener('keyup', e => {
    let keycode2 = e.keycode || e.which;;
    if (keycode2 == 13) {
        busqueda();
        search.value = "";
    }
    
})

/** PLAY SECTION 2 */

section2Play.addEventListener('click', e => {
    playSection2();
    botonPlayMusic();
    playMusic();
    audio.play();
});

function playSection2() {
    section2Play.classList.add('none');
    section2Play.classList.remove('show');
    section2Pause.classList.add('show');
}

/** PAUSE SECTION2 */
section2Pause.addEventListener('click', e => {
    pauseSection2();
    botonPauseMusic();
    pauseMusic();
    audio.pause();
    
});

function pauseSection2() {
    section2Play.classList.add('show');
    section2Play.classList.remove('none');
    
    section2Pause.classList.remove('show');
    section2Pause.classList.add('none');
}


/**VOLUMEN */
rango.addEventListener('input', e => {
    vol.innerText = rango.value;
    // console.log(audio.volume);
    audio.volume = rango.value / 100;
})

/** CADA UNA DE LAS BSUQUEDAS */

function btnMusic() {
    let btnMusic = document.querySelectorAll('.btn-music');
    let btnMusicPaused = document.querySelectorAll('.btn-music_paused');
    for (let i = 0; i < btnMusic.length; i++) {
        btnMusic[i].addEventListener('click', e => {
            img = musicas[i].artist.picture;
            imgPanel = musicas[i].artist.picture_big;
            cancion = musicas[i].preview;
            artistaP.innerText = musicas[i].title;
            imgFooter.src = musicas[i].artist.picture;
            fondoBoton.style.backgroundImage = "url("+img+")";
            fondoPanel.style.backgroundImage =  "url("+imgPanel+")";
            if (audio.src != cancion) {
                audio.src = cancion;
            }
            btnMusic[i].classList.add('none');
            btnMusic[i].classList.remove('show');
            btnMusicPaused[i].classList.remove('none');
            btnMusicPaused[i].classList.add('show');


            song.innerText = musicas[i].title;
            songInfo.innerText = musicas[i].artist.name + ' - ' + musicas[i].album.title;
            position = i;


            
            playSection2();
            playMusic();
            audio.play();
            

        });

        btnMusicPaused[i].addEventListener('click', e => {
            btnMusicPaused[i].classList.remove('show');
            btnMusicPaused[i].classList.add('none');
            btnMusic[i].classList.remove('none');
            btnMusic[i].classList.add('show');
            

            pauseSection2();
            pauseMusic();
            audio.pause();
        })
        
    }
    
}

function botonPlayMusic() {
    let btnMusic = document.querySelectorAll('.btn-music');
    let btnMusicPaused = document.querySelectorAll('.btn-music_paused');
    btnMusic[position].classList.add('none');
    btnMusic[position].classList.remove('show');
    btnMusicPaused[position].classList.remove('none');
    btnMusicPaused[position].classList.add('show');
}
function botonPauseMusic() {
    let btnMusic = document.querySelectorAll('.btn-music');
    let btnMusicPaused = document.querySelectorAll('.btn-music_paused');
    btnMusicPaused[position].classList.remove('show');
    btnMusicPaused[position].classList.add('none');
    btnMusic[position].classList.remove('none');
    btnMusic[position].classList.add('show');
}

/**CONSULTAR API */
async function busqueda() {
    const word = search.value ;
    if (word == null || word=="" ) {
        alert("introduce musica");
        
        
    } else {
        
        
        try {
            limpiar();
            const url2 = url + 'album:"'+word+'"';
            // let h = new Headers();
            // h.append('Accept', 'application/json');

            // let req = new Request(url2,{
            //     method:'GET',
            //     headers:h,
            //     mode:'CORS'
            // })
            const json = await getData(url2);
            btnMusic();
            return json;
        } catch (error) {
            console.log(error);
        }
    }
}

/** EXTRAER DATA */

function getData(url2) {
    return fetch(url2)
    .then(res => res.json())
    .then(data => {

        musicas = data.data;
        img = musicas[0].artist.picture;
        musica = musicas[0].title;
        artista = musicas[0].artist.name;
        imgPanel = musicas[0].artist.picture_big;
        audio.src = musicas[0].preview;
        fondoBoton.style.backgroundImage = "url("+img+")";
        fondoPanel.style.backgroundImage =  "url("+imgPanel+")";
        artistaP.innerText = musica;
        mejor.innerText = "Lo mejor de "+artista;



        /**footer */

        imgFooter.src = img;
        song.innerText = musicas[0].title;
        songInfo.innerText = musicas[0].artist.name + ' - ' + musicas[0].album.title
        
        for (let i = 0; i < musicas.length; i++) {
            // console.log(musicas[i].id);
            musics.innerHTML += `
                        <div class="music">
                            <div class="img-music">
                                <img class="img-music-imagen" src="${musicas[i].artist.picture}" alt="music">
                                <div class="puntos">
                                    <p>...</p>
                                </div>
                                <button class="btn-music">
                                    <i class="fas fa-play"></i>
                                </button>

                                <button class="btn-music_paused none">
                                    <i class="fas fa-pause"></i>
                                </button>
                                <input class="mp3" type="hidden" value="${musicas[i].preview}" />
                                
                            </div>
                            
                            <h3>${musicas[i].title}</h3>
                            <h4>${musicas[i].artist.name}</h4>
                        </div>
            
            
            `;

            

            
        }

        
    })
}

/**https://api.deezer.com/search?q=artist:%22%22album:%22mia%22 */

/**LIMPIAR RESULTADOS */
function limpiar() {
    musics.innerHTML = '';
}


/**Footer Reproductor */
play.addEventListener('click', e  => {
    
    playMusic();
    botonPlayMusic();
    playSection2();
    audio.play();

});

function playMusic() {
    play.classList.add('none');
    pause.classList.add('show');
    
}

/**PAUSE */
function pauseMusic() {
    play.classList.remove('none');
    pause.classList.remove('show');
    
}

pause.addEventListener('click', e  => {
    
    pauseMusic();
    botonPauseMusic()
    pauseSection2();
    audio.pause();
    
});
/** PARLANTE DE VOLUMEN */
parlante.addEventListener('click', e  => {
    
    
    parlante.classList.add('none');
    parlante.classList.remove('show');
    parlanteOff.classList.add('show');

    
    audio.muted = true;
    
      
    
    
});



parlanteOff.addEventListener('click', e  => {
    
    
    parlante.classList.add('show');
    parlante.classList.remove('none');

    parlanteOff.classList.remove('show');
    parlanteOff.classList.add('none');
    audio.muted = false;
    
    
});

